* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html,
body {
  width: 100vw;
  height: 100%;
  font-family: 'Rubik', sans-serif;
  overflow: hidden;
  background-color: $cc01;
}

a {
  text-decoration: none !important;
}

.kenburns_top_right {
  display: block;
  -webkit-animation: kenburns_top_right 5s ease-out both;
  animation: kenburns_top_right 5s ease-out both;
}

/**
 * ----------------------------------------
 * animation kenburns_top_right
 * ----------------------------------------
 */
@-webkit-keyframes kenburns_top_right {
  0% {
    -webkit-transform: scale(1) translate(0, 0);
    transform: scale(1) translate(0, 0);
    -webkit-transform-origin: 84% 16%;
    transform-origin: 84% 16%;
  }

  100% {
    -webkit-transform: scale(1.25) translate(20px, -15px);
    transform: scale(1.25) translate(20px, -15px);
    -webkit-transform-origin: right top;
    transform-origin: right top;
  }
}

@keyframes kenburns_top_right {
  0% {
    -webkit-transform: scale(1) translate(0, 0);
    transform: scale(1) translate(0, 0);
    -webkit-transform-origin: 84% 16%;
    transform-origin: 84% 16%;
  }

  100% {
    -webkit-transform: scale(1.25) translate(20px, -15px);
    transform: scale(1.25) translate(20px, -15px);
    -webkit-transform-origin: right top;
    transform-origin: right top;
  }
}