.cont {
    width: auto;
    height: auto;
    float: left;
    margin-right: 5vw;
    width: 100%;
    height: 100%;
    padding: 20% 0 0 30px;
    
    //lovely family
    .fathers {
        position: relative;
        width: max-content;
        height: auto;
        margin-left: 30%;
        margin-top: -13%;
        z-index: 0;


        div {
            position: absolute;

            &:nth-child(1n) {
                //wife
                height: auto;
                color: $cc01;
                svg {
                    transform: translateX(20%) translateY(-2%) scale(1.1) scaleX(-1);
                }
            }

            &:nth-child(2n) {
                //husband
                height: auto;
                color: $cc01;
                svg {
                    transform: translateX(-50%) translateY(-2%) scale(1.2) scaleX(-1);
                }
            }
        }
    }

    .cont_kids {
        position: absolute;
        top: calc(100% - 65%);
        width: 50%;
        height: 100%;
        z-index: 0;

            div {
                width: max-content;
                height: max-content;

                &:nth-child(1n) {
                    position: absolute;
                    top: -8%;
                    left: 0%;

                    svg {
                        transform: scale(1.2) scaleX(-1);
                    }

                }

                &:nth-child(2n) {
                    position: absolute;
                    top: -4%;
                    left: 45%;

                    svg {
                        transform: scale(1) scaleX(1);
                    }
                }

                &:nth-child(3n) {
                    position: absolute;
                    top: -4%;
                    left: 60%;

                    svg {
                        transform: scale(1.1) scaleX(-1);
                    }
                }

                &:nth-child(4n) {
                    position: absolute;
                    top: 4%;
                    left: 75%;

                    svg {
                        transform: scale(1) scaleX(1);
                    }
                }

                &:nth-child(5n) {
                    position: absolute;
                    top: -5%;
                    left: 30%;

                    svg {
                        transform: scale(1.2) scaleX(1);
                    }
                }

                &:nth-child(6n) {
                    position: absolute;
                    top: -3%;
                    left: 10%;

                    svg {
                        transform: scale(1.1) scaleX(-1);
                    }
                }

                &:nth-child(7n) {
                    position: absolute;
                    top: 8%;
                    left: 15%;

                    svg {
                        transform: scale(1.2) scaleX(1);
                    }
                }

                &:nth-child(8n) {
                    position: absolute;
                    top: 5%;
                    left: 30%;

                    svg {
                        transform: scale(1) scaleX(-1);
                    }
                }

                &:nth-child(9n) {
                    position: absolute;
                    top: 7%;
                    left: 45%;

                    svg {
                        transform: scale(.9) scaleX(1);
                    }
                }
            }

    }
}