.rotate_in_ver {
	-webkit-animation: rotate_in_ver 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	        animation: rotate_in_ver 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

/**
 * ----------------------------------------
 * animation rotate_in_ver
 * ----------------------------------------
 */
 @-webkit-keyframes rotate_in_ver {
    0% {
      -webkit-transform: rotateY(-360deg);
              transform: rotateY(-360deg);
      opacity: 0;
    }
    100% {
      -webkit-transform: rotateY(0deg);
              transform: rotateY(0deg);
      opacity: 1;
    }
  }
  @keyframes rotate_in_ver {
    0% {
      -webkit-transform: rotateY(-360deg);
              transform: rotateY(-360deg);
      opacity: 0;
    }
    100% {
      -webkit-transform: rotateY(0deg);
              transform: rotateY(0deg);
      opacity: 1;
    }
  }
  