.overlay {
    background: rgba(255, 255, 255, 0.5);
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 0 auto;
    z-index: 999;

    .overlay_cont {
        clear: both;
        width: min-content;
        height: auto;
        margin: 20vh auto;
        padding: 16px;
        background: rgba(255, 255, 255, 0.5);
        -webkit-backdrop-filter: blur(10px);
        backdrop-filter: blur(10px);
        border: 4px solid $cc07;
        border-radius: 10px;

        .cls_btn {
            float: right;
        }

        .cont_a {
            padding: 16px;
            clear: both;
            width: 100%;
            height: auto;

            .avatars {
                display: flex;
                justify-content: center;
                align-items: center;
                flex-wrap: nowrap;

                .avatar_w {
                    float: left;
                }

                .avatar_h {
                    float: left;
                }

            }
        }
    }

    .grd_cta {
        position: absolute;
        bottom: 16px;
        right: 16px;

        div {
            margin-left: 8px;
        }
    }
}