.card_1 {
    width: auto;
    height: auto;
    max-width: 300px;
    max-height: 300px;


    .avatar {
        clear: both;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        -moz-border-radius: 50%;
        -webkit-border-radius: 50%;
        box-shadow: 0.2rem 0.5rem 1.6rem var(--colorTrans0);
        max-width: 300px;
        max-height: 300px;

        svg {
            cursor: pointer;
            width: 100%;
            height: auto;
            border-radius: 50;
            -moz-border-radius: 50;
            -webkit-border-radius: 50;
        }

        .svg_int3_nu {

            path {
                fill: $cc01;
            }
        }

        .svg_int3_ru {

            path {
                fill: $cc01;
            }
        }
    }

    .bttm {

        h2 {
            clear: both;
            text-align: center;
            margin: 21px 8px;
        }
    }
}

.card_2 {
    height: auto;
    width: 100%;
    margin: 0 2%;

    .avatar {
        clear: both;
        width: 100%;
        border-radius: 50%;
        -moz-border-radius: 50%;
        -webkit-border-radius: 50%;
        box-shadow: 0.2rem 0.5rem 1.6rem var(--colorTrans0);

        svg {
            width: 100%;
            height: auto;
            border-radius: 50;
            -moz-border-radius: 50;
            -webkit-border-radius: 50;
        }
    }

    .quantity-input {
        margin: 15% 0;
    }
}

.card_3 {
    width: auto;
    height: auto;
    max-width: 150px;


    .avatar {
        clear: both;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        -moz-border-radius: 50%;
        -webkit-border-radius: 50%;
        box-shadow: 0.2rem 0.5rem 1.6rem var(--colorTrans0);
        max-width: 300px;
        max-height: 300px;
        margin: 0 auto;

        svg {
            cursor: pointer;
            width: 100%;
            height: auto;
            border-radius: 50;
            -moz-border-radius: 50;
            -webkit-border-radius: 50;
        }

        .svg_int3_nu {

            path {
                fill: $cc01;
            }
        }

        .svg_int3_ru {

            path {
                fill: $cc01;
            }
        }
    }

    .bttm {
        padding-top: 5%;
        display: flex;
        justify-content: center;
        align-content: center;
        justify-items: center;
        align-items: center;
    }
}