$border: 0 solid #dbdbdb;

.quantity-input {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.6rem;
  border-radius: 1em;
}

.quantity-input__modifier,
.quantity-input__screen {
  user-select: none;
  outline: none;
}

.quantity-input__modifier {
  padding: .7rem;
  width: 3rem;
  font-size: 1.6rem;
  
  background: $cc07;
  color: $cc01;
  border: $border;
  text-align: center;
  
  cursor: pointer;

  &--left {
    content: "-";
    border-radius: 1.5em 0 0 1.5em;
  }
  
  &--right {
    border-radius: 0 1.5em 1.5em 0;
  }
}

.quantity-input__screen {
  width: 4rem;
  padding: .6rem;
  font-size: 1.5rem;
  border: 2px solid $cc07;
  text-align: center;
}


