                        padding: 16px;
                        border-radius: 20px;
                        box-shadow: 0.2rem 0.8rem 1.6rem var(--colorTrans0);

                        .grd_icon {

                            align-items: center;
                            grid-area: grd_icon;

                            svg {
                                height: 56px;
                                width: 56px;
                                border-radius: 50%;
                                -moz-border-radius: 50%;
                                -webkit-border-radius: 50%;

                                .pathcc {
                                    fill: $cc07;
                                }
                            }
                        }

                        .grd_desc {

                            align-items: center;
                            padding-left: 21px;
                            grid-area: grd_desc;
                        }

                        .grd_cta {

                            align-items: center;
                            padding-left: 21px;
                            grid-area: grd_cta;


                            .input {
                                border: 2px solid #722022;
                                border-radius: 3rem;
                                margin-left: 8px;
                                margin-top: 25px;

                                .input_label {
                                    bottom: 60%;
                                    color: $cc07;
                                }

                                .input_num {
                                    display: block;
                                    margin: 0;
                                    padding: 7px;
                                    color: inherit;
                                    width: 100%;
                                    font-size: var(--inputFontSize);
                                    font-weight: inherit;
                                    line-height: var(--inputLineHeight);
                                    border: none;
                                    border-radius: 3rem;
                                    transition: box-shadow var(--transitionDuration);
                                    text-align: center;

                                    &::-webkit-outer-spin-button,
                                    &::-webkit-inner-spin-button {
                                        -webkit-appearance: none;
                                        margin: 0;
                                    }

                                    &[type=number] {
                                        -moz-appearance: textfield;
                                    }

                                    &::placeholder {
                                        color: #B0BEC5;
                                    }

                                    &:focus {
                                        outline: none;
                                        box-shadow: none;
                                    }
                                }
                            }

                            .custom_select {
                                margin: 0;
                            }
                        }
