.cur_cc04 {
    position: absolute;
    top:0;
    left: 0;
    width: 100vw;
    height: 100vh;

    .bk_left {
        float: left;
        width: 56%;
        height: 100vh;
        background-color: $cc04;
        clip-path: ellipse(93% 135% at 7% 0%);
    }

    .bk_right {
        float: left;
        width: 44%;
        height: 100vh;
    }
}

