.btn {
    display: inline-block;
    padding: 0.3em 1.2em;
    margin: 0 0.1em 0.1em 0;
    border: 4px solid rgba(255, 255, 255, 0);
    border-radius: 2em;
    color: #FFFFFF;
    background: $cc07;
    box-shadow: 0.2rem 0.5rem 1.6rem var(--colorTrans0);
    text-align: center;
    color: $cc01;
    font-size: 1.5em;
    font-weight: 400;
    font-style: normal;
    transition: all 0.2s;

    &:hover {
        cursor: pointer;
        color: $cc07;
        border-color: $cc07;
        background-color: transparent;
        box-shadow: none;
    }

    @media all and (max-width:30em) {
        display: block;
        margin: 0.2em auto;
    }
}



.btn_circle {
    background-color: $cc07;
    border: 4px solid rgba(255, 255, 255, 0);
    height: 8vw;
    width: 8vw;
    max-width: 56px;
    max-height: 56px;
    min-width: 36px;
    min-height: 36px;
    overflow: hidden;
    margin: 0 10%;
    float: left;
    border-radius: 50%;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    box-shadow: 0.2rem 0.8rem 1.6rem var(--colorTrans0);
    z-index: 9999;

    svg {
        width: 100%;
        height: 100%;
        height: auto;
        margin: 0 auto;

        path {
            fill: $cc02;
        }
    }

    &:hover {
        cursor: pointer;
        color: $cc07;
        border-color: $cc07;
        background-color: transparent;
        box-shadow: none;
        border-color: $cc07;
        z-index: 999;

        svg {
    
            path {
                fill: $cc07;
            }
        }
    }
}