$text-color: mix(#000, $cc07, 64%);

.r_bttons {
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;

    label {
        display: flex;
        cursor: pointer;
        font-weight: 500;
        position: relative;
        overflow: hidden;
        font-size: 1.8em;
        color: $cc00;
        margin-bottom: 0.375em;
        /* Accessible outline */
        /* Remove comment to use */ 
        /*
            &:focus-within {
                    outline: .125em solid $cc07;
            }
        */
        input {
            position: absolute;
            left: -9999px;
            &:checked + span {
                background-color: mix(#fff, $cc07, 84%);
                &:before {
                    box-shadow: inset 0 0 0 0.4375em $cc07;
                }
            }
        }
        span {
            display: flex;
            align-items: center;
            padding: 0.375em 0.75em 0.375em 0.375em;
            border-radius: 99em; // or something higher...
            transition: 0.25s ease;
            &:hover {
                background-color: mix(#fff, $cc07, 84%);
            }
            &:before {
                display: flex;
                flex-shrink: 0;
                content: "";
                background-color: #fff;
                width: 1.5em;
                height: 1.5em;
                border-radius: 50%;
                margin-right: 0.375em;
                transition: 0.25s ease;
                box-shadow: inset 0 0 0 0.125em $cc07;
            }
        }
    }
}