.lyts {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto 1fr auto;
    grid-auto-columns: 1fr;
    grid-auto-rows: 1fr;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
        "grd_top_nav"
        "grd_cont"
        "grd_bottom_nav";

    width: 100vw !important;
    height: 100vh !important;
    position: absolute;
    top: 0;
    left: 0;

    .grd_top_nav {
        grid-area: grd_top_nav;
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto;
        grid-auto-columns: 1fr;
        grid-auto-rows: 1fr;
        gap: 0px 0px;
        grid-auto-flow: row;
        grid-template-areas:
            "grd_left_top grd_right_top";

        .grd_left_top {
            text-align: left;
            padding: 16px 0 16px 24px;
            grid-area: grd_left_top;

            svg {
                height: 2vw;
                width: 2vw;
                border-radius: 50%;
                -moz-border-radius: 50%;
                -webkit-border-radius: 50%;

                .pathcc {
                    fill: $cc01;
                }
            }
        }

        .grd_right_top {
            text-align: right;
            padding: 16px 24px 0 0;
            grid-area: grd_right_top;

            div {
                float: right;
            }
        }

    }

    .grd_cont {
        margin: 0 auto;
        min-width: calc(100% - 30%);
        grid-area: grd_cont;

        //LYT INT1
        .int1 {
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: auto 1fr;
            grid-auto-columns: 1fr;
            grid-auto-rows: 1fr;
            gap: 0px 0px;
            grid-auto-flow: row;
            grid-template-areas:
                "grd_line1"
                "grd_line2";
            height: 100%;
            //max-width: 1028px;

            .grd_line1 {
                display: flex;
                justify-self: center;
                align-items: center;
                grid-area: grd_line1;

                h1 {
                    font-size: $HFontSize_1;
                }
            }

            .grd_line2 {
                width: 100%;
                height: 100%;
                max-width: 1301px !important;
                max-height: 734px !important;
                grid-area: grd_line2;

                .player-wrapper {
                    width: 100%;
                    position: relative;
                    padding-top: 56.25%;
                    /* 720 / 1280 = 0.5625 */

                    .video_player {
                        position: absolute;
                        top: 5%;
                        left: 5%;
                        width: 90% !important;
                        height: 90% !important;
                        max-width: 1301px;
                        max-height: 734px;
                    }
                }
            }
        }

        //[END] LYT INT1

        //LYT INT2
        .int2 {
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: auto 1fr;
            grid-auto-columns: 1fr;
            grid-auto-rows: 1fr;
            gap: 0px 0px;
            grid-auto-flow: row;
            grid-template-areas:
                "grd_line1"
                "grd_line2";
            height: 100%;
            //max-width: 1028px;

            .grd_line1 {
                place-items: center;
                grid-area: grd_line1;

                h1 {
                    font-size: $HFontSize_1;
                    text-align: center;
                }
            }

            .grd_line2 {
                place-items: center;
                grid-area: grd_line2;

                .cont {
                    align-items: center;
                    display: grid;
                    grid-template-columns: 1fr;
                    grid-template-rows: 1fr;
                    gap: 0px 0px;
                    grid-template-areas:
                        "grd_cards";
                    padding: 0;
                    margin: 0;

                    .grd_cards {
                        display: grid;
                        grid-template-columns: 1fr 1fr;
                        grid-template-rows: 1fr;
                        gap: 0px 0px;
                        grid-template-areas:
                            "grd_c1 grd_c2";

                        grid-area: grd_cards;

                        .grd_c1 {
                            grid-area: grd_c1;
                            justify-items: center;

                            div {
                                margin: 0 auto;
                                padding: 0;
                            }
                        }

                        .grd_c2 {
                            justify-items: center;
                            grid-area: grd_c2;

                            div {
                                margin: 0 auto;
                                padding: 0;
                            }
                        }
                    }
                }
            }
        }

        //[END] LYT INT2

        //LYT INT3
        .int3 {
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: auto 1fr;
            grid-auto-columns: 1fr;
            grid-auto-rows: 1fr;
            gap: 0px 0px;
            grid-auto-flow: row;
            grid-template-areas:
                "grd_line1"
                "grd_line2";
            height: 100%;
            //max-width: 1028px;

            .grd_line1 {
                display: flex;
                justify-self: center;
                align-items: center;
                grid-area: grd_line1;

                h1 {
                    font-size: $HFontSize_1;
                    text-align: center;
                }
            }

            .grd_line2 {
                justify-content: center;
                align-content: center;
                justify-items: center;
                align-items: center;
                grid-area: grd_line2;

                .cont {
                    padding-top: 20vh;
                    display: grid;
                    grid-template-columns: 1fr;
                    grid-template-rows: 1fr;
                    gap: 0px 0px;
                    grid-template-areas:
                        "grd_cards";

                    .grd_cards {
                        display: grid;
                        grid-template-columns: 1fr 1fr;
                        grid-template-rows: 1fr;
                        gap: 0px 0px;
                        grid-template-areas:
                            "grd_c1 grd_c2";

                        grid-area: grd_cards;

                        .grd_c1 {
                            grid-area: grd_c1;
                            justify-content: center;

                            div {
                                margin: 0 auto;
                            }
                        }

                        .grd_c2 {
                            grid-area: grd_c2;

                            div {
                                margin: 0 auto;
                            }
                        }
                    }
                }
            }
        }

        //[END] LYT INT3

        //LYT SS1
        .ss1 {
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: auto auto 1fr auto;
            grid-auto-columns: auto;
            grid-auto-rows: auto;
            gap: 0px 0px;
            grid-auto-flow: row;
            grid-template-areas:
                "grd_line1"
                "grd_line2"
                "grd_line3";
            height: 100%;

            .grd_line1 {
                display: flex;
                justify-self: center;
                align-items: center;
                grid-area: grd_line1;

                h1 {
                    font-size: $HFontSize_1;
                    padding: 0 2em;
                    text-align: center;
                }
            }

            .grd_line2 {
                display: flex;
                justify-self: center;
                align-items: center;
                grid-area: grd_line2;

                h2 {
                    font-size: $HFontSize_2;
                    margin: 0.5em 1em 0.5em;
                    text-align: center;
                }
            }

            .grd_line3 {
                grid-area: grd_line3;
                height: 100%;

                .cont_avatars {
                    display: grid;
                    grid-template-columns: 1fr 1fr 1fr;
                    grid-template-rows: 1fr;
                    grid-auto-columns: 1fr;
                    gap: 0px 0px;
                    grid-auto-flow: row;
                    grid-template-areas:
                        "a1 a2 a3";
                    height: 100%;

                    .a1 {
                        justify-self: center;
                        align-self: center;
                        grid-area: a1;

                        div {
                            svg {
                                transform: scale(1.4);
                            }
                        }
                    }

                    .a2 {
                        justify-self: center;
                        align-self: center;
                        grid-area: a2;

                        div {
                            svg {
                                transform: scale(1.4);
                            }
                        }
                    }

                    .a3 {
                        justify-self: center;
                        align-self: center;
                        grid-area: a3;

                        div {
                            svg {
                                transform: scale(1.4);
                            }
                        }
                    }
                }
            }
        }

        //[END] LYT SS1

        //LYT SS1.1
        .ss1_1 {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-template-rows: 100%;
            grid-auto-columns: 1fr;
            grid-auto-rows: 1fr;
            gap: 0px 2%;
            grid-auto-flow: row;
            grid-template-areas:
                "grd_left grd_right";
            height: 100%;
            //max-width: 1028px;

            .grd_left {
                display: flex;
                justify-self: right;
                align-items: center;
                grid-area: grd_left;

                div {
                    background-color: $cc01;
                    height: 40vw;
                    width: 40vw;
                    max-width: 500px;
                    max-height: 500px;
                    border-radius: 5px;
                    overflow: hidden;
                    margin: 50px 0;
                    float: right;
                    border-radius: 50%;
                    -moz-border-radius: 50%;
                    -webkit-border-radius: 50%;
                    box-shadow: 0.2rem 0.5rem 1.6rem var(--colorTrans0);
                    background-color: $cc03;

                    svg {
                        padding-top: 5vw;
                        width: 100%;
                        height: auto;
                        margin: 0 auto;
                    }
                }
            }

            .grd_right {
                display: flex;
                justify-self: left;
                align-items: center;
                grid-area: grd_right;

                .cont {
                    width: 100%;
                    height: auto;
                    padding: 10%;

                    h1 {
                        font-size: $HFontSize_1;
                        padding: 50px 0;
                        margin-left: 8px;
                        text-align: left;
                    }

                    .form {
                        width: 100%;
                        height: auto;
                        max-width: 600px;

                        .flds {
                            padding: 8px 8px 32px;
                            float: left;

                            &:nth-child(1n) {
                                width: 75%;
                            }

                            &:nth-child(2n) {
                                width: 25%;
                            }
                        }

                        .qsts {
                            clear: both;
                            width: 75%;
                            padding: 0 8px;

                            h3 {
                                margin: 16px;
                                width: 100%;
                            }
                        }

                    }
                }

            }
        }

