                    }
                }
            }

            .grd_right {
                place-items: center;
                grid-area: grd_right;

                .cont {
                    width: 100%;
                    height: auto;
                    max-width: 600px;
                    padding: 10%;

                    h1 {
                        font-size: $HFontSize_1;
                        padding: 50px 0;
                        margin-left: 8px;
                        text-align: left;
                    }

                    .form {

                        .flds {
                            padding: 8px 0 32px;
                        }
                    }
                }

            }
        }

        //[END] LYT SS1.3

        //LYT SS1.4
        .ss1_4 {
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: auto 1fr;
            grid-auto-columns: 1fr;
            grid-auto-rows: 1fr;
            gap: 0px 0px;
