        }

        //[END] LYT SS1.4

        //LYT SS1.5
        .ss1_5 {
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: auto auto auto 1fr;
            grid-auto-columns: 1fr;
            grid-auto-rows: 1fr;
            gap: 0px 0px;
            grid-auto-flow: row;
            grid-template-areas:
