                div {
                    float: left;
                    margin: 0 8px;
                    min-width: 150px;
                }

            }

            .grd_line3 {
                display: flex;
                justify-content: center;
                align-items: center;
                flex-wrap: nowrap;
                padding: 5vh 0 0;
                grid-area: grd_line3;

                h2 {
                    text-align: center;
                }
            }

            .grd_line4 {
                padding: 0 1em 0.5em;
                justify-content: center;
                justify-self: center;
                grid-area: grd_line4;

                div {
                    min-width: 180px;
                    float: left;

                    &:nth-child(1n) {
                        margin-right: 5px;
                    }

                    &:nth-child(2n) {
                        margin-left: 5px;
                    }
                }

            }
        }

        //[END] LYT SS1.5

        //LYT SS1.6
        .ss1_6 {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-template-rows: 100%;
