                //lovely family
            }

            .grd_right {
                grid-area: grd_right;

                .cont {
                    width: 100%;
                    height: 100%;
                    padding: calc(.5rem + var(--paddingXxlH1));

                    h1 {
                        font-size: $HFontSize_1;
                        margin-bottom: 5vh;
                        text-align: left;
                    }

