                    width: calc(5rem + 2vw);
                    height: auto;
                    border: 4px var(--colorTrans7) solid;
                    margin: 0.5rem;
                    border-radius: 10px;
                    padding: 0.5rem;
                    cursor: pointer;
                    position: relative;
                }
            }

