                        height: auto;
                        border: 4px var(--colorTrans7) solid;
                        margin: 0.5rem;
                        border-radius: 10px;
                        padding: 0.5rem;
                        cursor: pointer;
                        position: relative;
                    }
                }
            }

            .grd_cta {
                justify-self: center;
                align-self: center;
                grid-area: grd_cta;
            }
        }

