            }

            .grd_right {
                grid-area: grd_right;

                .cont {
                    width: 100%;
                    height: auto;
                    max-width: 500px;
                    padding: 16px;

                    h1 {
                        font-size: $HFontSize_1;
                        padding: 100px 0 50px;
                        text-align: left;
                    }

                    .flds {
                        margin: 0 0 21px;
