        //[END] LYT FP43

        //LYT FP44
        .fp44 {
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: auto 1fr;
            grid-auto-columns: 1fr;
            grid-auto-rows: auto;
            gap: 0px 0px;
            grid-auto-flow: row;
            grid-template-areas:
                "grd_line1"
                "grd_line2";
            height: 100%;
            //max-width: 1028px;

            .grd_line1 {
                padding-bottom: 12vh;
                grid-area: grd_line1;

                h1 {
                    font-size: $HFontSize_1;
                    margin: 0 0 20px;
                    padding: 0 1em 0.5em;
                    text-align: center;
                }
            }

            .grd_line2 {
                padding: 0 calc(.5rem + 23vw);
                grid-area: grd_line2;

                .mbl {
                    display: grid;
                    grid-template-columns: auto 1fr auto;
                    grid-template-rows: 1fr;
                    gap: 0px 0px;
                    grid-template-areas:
                        "grd_icon grd_desc grd_cta";

                    background-color: #fff;
                    margin: 0 32px 21px;
                    padding: 16px;
                    border-radius: 20px;
                    box-shadow: 0.2rem 0.8rem 1.6rem var(--colorTrans0);

                    .grd_icon {

                        align-items: center;
                        grid-area: grd_icon;

                        svg {
                            height: 100px;
                            width: 100px;
                            border-radius: 50%;
                            -moz-border-radius: 50%;
                            -webkit-border-radius: 50%;
                            transform: scale(1.2);

                            .pathcc {
                                fill: $cc07;
                            }
                        }
                    }

                    .grd_desc {
                        font-size: $HFontSize_3;
                        align-items: center;
                        padding: 0 5%;
                        grid-area: grd_desc;
                    }

                    .grd_cta {

                        align-items: center;
                        //padding-left: 21px;
                        grid-area: grd_cta;

                        div {

                            .flds {
                                background-color: red;
                            }

                            .input {
                                border: 2px solid #722022;
                                border-radius: 3rem;
                                margin-top: 25px;

                                .input_label {
                                    bottom: 60%;
                                    color: $cc07;
                                }

                                .input_num {
                                    display: block;
                                    margin: 0;
                                    padding: 7px;
                                    color: inherit;
                                    width: 100%;
                                    font-size: var(--inputFontSize);
                                    font-weight: inherit;
                                    line-height: var(--inputLineHeight);
                                    border: none;
                                    border-radius: 3rem;
                                    transition: box-shadow var(--transitionDuration);
                                    text-align: center;
                                    margin: 0;

                                    &::-webkit-outer-spin-button,
                                    &::-webkit-inner-spin-button {
                                        -webkit-appearance: none;
                                        margin: 0;
                                    }

                                    &[type=number] {
                                        -moz-appearance: textfield;
                                    }

                                    &::placeholder {
                                        color: #B0BEC5;
                                    }

                                    &:focus {
                                        outline: none;
                                        box-shadow: none;
                                    }
                                }
                            }

                            .custom_select {
                                padding: 7px;
                                margin: 0 0 0 2px;
                            }

                        }
                    }
                }
            }
        }

        //[END] LYT FP44

        //LYT FP45
        .fp45 {
            display: flex;
            justify-self: center;
            align-items: center;

            .cont {
                display: grid;
                grid-template-columns: auto auto;
                grid-template-rows: auto auto;
                gap: 0 0;
                grid-template-areas:
                    "grd_title1 grd_title2"
                    "grd_r grd_date";
                background-color: $ccw;
                border-radius: 20px;
                box-shadow: 0.2rem 0.8rem 1.6rem var(--colorTrans0);
                margin: 16vh auto;
                padding: 0;

                .grd_title1 {
                    padding: 16px 16px 0;
                    border-radius: 20px 0 0;
                    grid-area: grd_title1;

                    h2 {
                        font-size: $HFontSize_2;
                        margin-bottom: 0;
                    }
                }

                .grd_title2 {
                    padding: 16px 16px 0;
                    border-radius: 0 20px 0 0;
                    background-color: mix(#fff, $cc07, 84%);
                    grid-area: grd_title2;

                    h2 {
                        margin-bottom: 0;
                    }
                }

                .grd_r {
                    display: flex;
                    justify-self: center;
                    align-items: center;
                    align-items: center;
                    grid-area: grd_r;

                    div {
                        margin: 0 8px;
                    }
                }

                .grd_date {
                    padding: 16px;
                    border-radius: 0 0 20px;
                    background-color: mix(#fff, $cc07, 84%);
                    grid-area: grd_date;
                }
            }
        }

        //[END] LYT FP45

        //LYT RSS1
        .rss1 {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-template-rows: 100%;
            grid-auto-columns: 1fr;
            grid-auto-rows: 1fr;
            gap: 0px 2%;
            grid-auto-flow: row;
            grid-template-areas:
                "grd_left grd_right";
            height: 100%;

            .grd_left {
                display: flex;
                justify-self: right;
                align-items: center;
                grid-area: grd_left;

                div {
                    background-color: $cc01;
                    height: 40vw;
                    width: 40vw;
                    max-width: 500px;
                    max-height: 500px;
                    border-radius: 5px;
                    overflow: hidden;
                    margin: 50px 0;
                    float: right;
                    border-radius: 50%;
                    -moz-border-radius: 50%;
                    -webkit-border-radius: 50%;
                    box-shadow: 0.2rem 0.5rem 1.6rem var(--colorTrans0);
                    background-color: $cc03;

                    svg {
                        padding-top: 5vw;
                        width: 100%;
                        height: auto;
                        margin: 0 auto;
                    }
                }
            }

            .grd_right {
                display: flex;
                justify-self: left;
                align-items: center;
                grid-area: grd_right;

                .cont {
                    width: 100%;
                    height: auto;
                    padding: 10%;

                    h1 {
                        font-size: $HFontSize_1;
                        padding: 50px 0;
                        margin-left: 8px;
                        text-align: left;
                    }

                    .form {
                        width: 100%;
                        height: auto;
                        max-width: 600px;

                        .flds {
                            padding: 8px 8px 32px;
                            float: left;

                            &:nth-child(1n) {
                                width: 75%;
                            }

                            &:nth-child(2n) {
                                width: 25%;
                            }
                        }

                        .qsts {
                            clear: both;
                            width: 75%;
                            padding: 0 8px;

                            h3 {
                                margin: 16px;
                                width: 100%;
                            }
                        }

                    }
                }

            }
        }

        //[END] LYT RSS1

        //LYT RSS2
        .rss2 {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-template-rows: 100%;
            grid-auto-columns: 1fr;
            grid-auto-rows: 1fr;
            gap: 0px 40px;
            grid-auto-flow: row;
            grid-template-areas:
                "grd_left grd_right";
            height: 100%;

            .grd_left {
                grid-area: grd_left;

                //lovely family
            }

            .grd_right {
                padding: 6% 2% 0 12%;
                grid-area: grd_right;

                h1 {
                    font-size: $HFontSize_2;
                    margin-bottom: 5vh;
                    text-align: left;
                }

                h2 {
                    font-size: $HFontSize_2;
                    margin-bottom: 5vh;
                    text-align: left;
                }
            }

        }

        //[END] LYT RSS2

        //LYT RSS9/10
        .rss9__10 {
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: 1fr auto 1fr;
            gap: 0px 0px;
            grid-auto-flow: row;
            grid-template-areas:
                "grd_rw1"
                "grd_rw2"
                "grd_rw3";
            height: 100%;
            //max-width: 1028px;

            .grd_rw1 {
                grid-area: grd_rw1;

                div {
                    width: 25% !important;
                    height: auto;
                    margin: 0 auto;

                    svg {
                        width: 100% !important;
                        height: 100% !important;

                        path {
                            fill: $cc01;
                        }
                    }
                }


            }

            .grd_rw2 {
                justify-self: center;
                align-self: start;
                grid-area: grd_rw2;

                h1 {
                    font-size: $HFontSize_1;
                    padding: 0 1em;
                    text-align: center;
                }

                h2 {
                    font-size: $HFontSize_2;
                    text-align: center;
                    margin: 2% 1% 0;
                }
            }

            .grd_rw3 {
                display: flex;
                justify-self: center;
                align-items: center;
                grid-area: grd_rw3;
            }
        }

        //[END] LYT RSS9/10
    }

    .grd_bottom_nav {
        grid-area: grd_bottom_nav;

        .btm_nab_one {
            display: grid;
            grid-template-columns: auto 1fr auto;
            grid-template-rows: 100%;
            grid-auto-columns: 1fr;
            grid-auto-rows: 1fr;
            gap: 0px 0px;
            grid-auto-flow: row;
            grid-template-areas:
                "grd_btm_left grd_btm_center grd_btm_right";
            height: 100%;
            //display: none;



            .grd_btm_left {
                display: flex;
                justify-self: center;
                align-items: center;
                padding: 0 0 16px 30px;
                grid-area: grd_btm_left;
            }

            .grd_btm_center {
                display: flex;
                justify-self: center;
                align-items: center;
                padding: 0 8px 14px;
                grid-area: grd_btm_center;
            }

            .grd_btm_right {
                display: flex;
                justify-self: center;
                align-items: center;
                padding: 0 30px 16px 0;
                grid-area: grd_btm_right;
            }
        }
    }

}